<template>
  <div>
    <div v-if="isIssue">
      <v-contextmenu-item disabled></v-contextmenu-item>
    </div>
    <div v-else>
      <v-contextmenu-item @click="issue" v-if="!disableIssue"></v-contextmenu-item>
      <v-contextmenu-item @click="createMD" v-if="!disableFile"></v-contextmenu-item>
      <v-contextmenu-item @click="revisions" v-if="!disableFolder"></v-contextmenu-item>
      <v-contextmenu-item @click="renameFile" v-if="!disableDelete"></v-contextmenu-item>
      <v-contextmenu-item @click="renameFile" v-if="!disableDelete" divider></v-contextmenu-item>
      <v-contextmenu-item @click="removeFile" v-if="!disableRemove"></v-contextmenu-item>
    </div>
  </div>
</template>

<script>
  export default {
    name: "contextmenu",
    props: ['disableFile', 'disableFolder', 'disableIssue', 'disableDelete', 'disableRemove', 'isIssue'],
    methods: {
      issue() {
        this.$emit('issue');
      },
      createMD() {
        this.$emit('createMD');
      },
      revisions() {
        this.$emit('revisions');
      },
      renameFile() {
        this.$emit('renameFile');
      },
      removeFile() {
        this.$emit('removeFile');
      }
    }
  }
</script>

<style scoped>

</style>



